const utils = require('./utils.js');

$(document).ready(function () {
  var filters = {"Class": "All", "Era": "All"};
  var viewport = utils.findBootstrapEnvironment();
  var options = {
    valueNames: [ 'wrestler-name', 'wrestler-class', 'wrestler-era' ],
    page: 10,
    pagination: [{
      paginationClass: "paginationTop",
      innerWindow: 1
    }, {
      paginationClass: "paginationBottom",
      innerWindow: 1
    }]
  };
  var userList = new List('wrestlers', options);

  var acmInput = document.getElementById("search");
  var acm = new Awesomplete(acmInput, {list: "#wrestlersList"});

  $('#search').on("submit", function(event){
    event.preventDefault();
    return false;
  });

  $('a.filter-by-era').on("click", function(event){
    filters.Era = $(this).attr("data-class-id");
    $('button.filter-by-era-button').html($(this).text() + " <span class='caret'></span>");
    updateFilter();
    event.preventDefault();
  });

  $('a.filter-by-class').on("click", function(event){
    filters.Class = $(this).attr("data-class-id");
    $('button.filter-by-class-button').html($(this).text() + " <span class='caret'></span>");
    updateFilter();
    event.preventDefault();
  });

  function updateFilter() {
    var wrestlerList = [];
    if (filters.Class == "All" && filters.Era == "All") {
      userList.filter(function(item){
        wrestlerList.push(item.values()["wrestler-name"]);
        return true;
      });
    } else {
      userList.filter(function(item){
        if (filters.Class != "All" && filters.Era == "All") {
          if (item.values()["wrestler-class"] == filters.Class) {
            wrestlerList.push(item.values()["wrestler-name"]);
            return true;
          } else {
            return false;
          }
        } else if (filters.Era != "All" && filters.Class == "All") {
          if (item.values()["wrestler-era"] == filters.Era) {
            wrestlerList.push(item.values()["wrestler-name"]);
            return true;
          } else {
            return false;
          }
        } else if (filters.Class != "All" && filters.Era != "All") {
          if (item.values()["wrestler-class"] == filters.Class && item.values()["wrestler-era"] == filters.Era) {
            wrestlerList.push(item.values()["wrestler-name"]);
            return true;
          } else {
            return false;
          }
        }
      });
    }
    acm.list = wrestlerList;
  }

  $('#search').on('keyup keypress', function(e) {
    var keyCode = e.keyCode || e.which;
    if (keyCode === 13) {
      e.preventDefault();
      return false;
    }
  });

  $("#search").on('awesomplete-selectcomplete',function(){
    userList.search(this.value, ['wrestler-name']);
  });

  var myLazyLoad = new LazyLoad();

  userList.on('updated', function(thisList){
    myLazyLoad.update();
  });

  if (viewport == "xs") {
    $('#search').on('focus', function() {
      document.body.scrollTop += this.getBoundingClientRect().top - 10
    });
  }

  $('#search').on('input propertychange', function() {
    var $this = $(this);
    var visible = Boolean($this.val());
    $('.form-control-clear').toggleClass('hidden', !visible);
  }).trigger('propertychange');

  $('.form-control-clear').click(function() {
    userList.search('');
    $('#search').val('').trigger('propertychange').focus();
  });
});



